"use client";

import { useState } from "react";
import { Etapa0 } from "./components/Etapa0";
import { Etapa1 } from "./components/Etapa1";
import { Etapa2 } from "./components/Etapa2";

interface FormData {
  nome: string;
  celular: string;
  sexo: string;
  email: string;
  voluntario: boolean | undefined;
  camiseta: string;
  membroDesde: string;
  voluntarioDesde: string;
  ministerio: string[];
  batizado: boolean;
  batizadoDesde: string;
}

export default function FormularioPage() {
  const [etapa, setEtapa] = useState(0);
  const [form, setForm] = useState<FormData>({
    nome: "",
    celular: "",
    sexo: "",
    email: "",
    voluntario: undefined,
    camiseta: "",
    membroDesde: "",
    voluntarioDesde: "",
    ministerio: [],
    batizado: false,
    batizadoDesde: "",
  });

  const handleChange = (e: React.ChangeEvent<any>) => {
    const { name, value, type, checked } = e.target;
    
    // Tratamento genérico para diferentes tipos de input
    let finalValue = value;
    if (name === "voluntario" || name === "batizado") {
        finalValue = (value === "true" || value === true);
    } else if (type === "checkbox") {
        // Se for o toggle de ministério, 'value' já é o array atualizado
        finalValue = name === "ministerio" ? value : checked;
    }

    setForm((prev) => ({ ...prev, [name]: finalValue }));
  };

  const handleNext = () => setEtapa((prev) => prev + 1);
  const handleBack = () => setEtapa((prev) => prev - 1);

  const handleSubmit = async () => {
    console.log("Formulário enviado:", form);
    alert("Inscrição enviada com sucesso!");
  };

  return (
    <div className="container mx-auto p-8 max-w-2xl">
      {/* Renderização condicional das etapas com a PROP CORRIGIDA */}
      {etapa === 0 && (
        <Etapa0
          handleChange={handleChange}
          onNext={handleNext}
          form={form} // <-- CORREÇÃO APLICADA AQUI
        />
      )}
      {etapa === 1 && (
        <Etapa1
          form={form as FormData & { voluntario: boolean }}
          handleChange={handleChange}
          onNext={handleNext}
          onBack={handleBack}
        />
      )}
      {etapa === 2 && (
        <Etapa2
          form={form as FormData & { voluntario: boolean }}
          handleChange={handleChange}
          onBack={handleBack}
          onSubmit={handleSubmit}
        />
      )}
    </div>
  );
}